@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    background-color: v.$coral;
    &__contenido {
        @include m.contenedor;
        display: flex;
        flex-direction: column;

        @include m.desktop {
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
        }
    }

}